* {
    font-family: 'Maven Pro';
}

h1 {
    color: green;
    font-style: bold;
}

button {
    cursor: pointer;
}


.movie_mainview {
    display: grid;
    column-gap: 0.5rem;
    grid-template-columns: 2fr 1fr;
    width: auto;
}

.centercontent {
    margin-left: 10%;
    margin-right: 10%;
    padding: 0.5rem;
    background-color: antiquewhite;
}

.movie_postercontainer {
    background-color: white;
    padding: 0.25rem;
    border-radius: 0.2rem;
}

.movie_cast {
    background-color: white;
    padding: 0.25rem;
    border-radius: 0.2rem;
    overflow-x: scroll;
    display: grid;
    white-space: nowrap;
    width: 50rem;
}

.movie_cast_scroll {
    // display: grid;
    // white-space: nowrap;
    // background-color: aqua;
}

.movie_cast_scroll hr {
    width: 100%;
}

.scroll {
   width: 100%; 
}

.actor_div {
    display: inline-block;
    width: auto;
    background-color: white;
    margin: 0.5rem;
    padding: 0.5rem;
}

.actor_div img{
    border-radius: 50px;

}

.section {
    width: 100%;
}


// Testing for video
.movie_videoview {
    display: grid;
    column-gap: 0.5rem;
    grid-template-columns: 0.8fr 2fr;
    width: auto;
}

.movie_videoview_title {
    display: grid;
    column-gap: 0.5rem;
    grid-template-columns: 5fr 1fr;
    width: auto;
}

.video_button {
    margin-top: 2rem;
    height: 50%;
}

#black_bg {
    position: fixed; /* Sit on top of the page content */
    display: none; /* Hidden by default */
    width: 100%; /* Full width (cover the whole page) */
    height: 100%; /* Full height (cover the whole page) */
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.5); /* Black background with opacity */
    z-index: 2; /* Specify a stack order in case you're using a different order for other elements */
    cursor: pointer; /* Add a pointer on hover */
}

#movie_popup {
    width: 80%;
    height: 80%;
    position: fixed;
    top: 10%;
    left: 10%;
}

#movie_popup iframe {
    width: 100%;
    height: 100%;
}

#close_popup_button {
    position: fixed;
    left: 89%;
    top: 9%;
    z-index: 5;
    border-radius: 1rem;
    width: 2rem;
    height: 2rem;
    font-size: large;
}

.site_header {
    display: grid;
    margin-left: 10%;
    margin-right: 10%;
    padding: 0.5rem;
    background-color: rgb(225, 178, 219);
    grid-template-columns: 2fr 3fr 1fr 1fr 1fr;
}

.site_header * {
    color:black;
}

.cast_movies {
    display: grid;
    padding: 0.5rem;
    column-gap: 0.5rem;
    row-gap: 2rem;
    background-color: rgb(225, 178, 219);
    grid-template-columns: 2fr 2fr 2fr;

}

// INCLUDES MOVIES
.movie_parts_list {
    display: grid;
    padding: 0.5rem;
    column-gap: 0.5rem;
    row-gap: 2rem;
    background-color: rgb(225, 178, 219);
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    // grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}

.movie_part_item {
    display: grid;
    row-gap: 0rem;
    background-color: rgb(225, 178, 219);
    grid-template-rows: 1fr 0.1fr;
}


// HOME PAGE

.home_image {
    border-radius: 1rem;
    vertical-align: top;
    width: 100%;
    filter: brightness(50%);
    mask-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0));
}

// .landing_items {
//     position: relative;
//     text-align: center;
//     top: 50%;
//     left: 50%;
//     width: 100%;
//     height: 100%;
// }

.landing_items {
    position: relative;
    text-align: center;
    top: 50%;
    width: 100%;
    height: 100%;
}

.text_over_image {
    color: white;
    position: absolute;  
    top: 40%;
    left: 50%;
    transform: (translate(-50%, -50%));
    text-align: center;
}

.text_over_image h1{
    color: white;

}

.fade {
    width: 100%;
    height: 100%;
    top: 5%;
    left: 50%;
    position: absolute;
    background: -webkit-linear-gradient(left, rgba(0,0,0, 0.65) 0%, rgba(0,0,0,0) 20%, rgba(0,0,0,0) 80%, rgba(0,0,0,0.65) 100%);
}

//EXPLORE
#movies_list {
    list-style-type: none;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(min-content, 150px));
    /* grid-template-columns: 1fr 1fr 1fr 1fr; */
    column-gap: 1rem;
    row-gap: 1rem;
    padding: 0;
    margin: 0;
  }
  
  #movies_list li {
    border: 1px solid #ddd;
    margin-top: -1px; /* Prevent double borders */
    background-color: #f6f6f6;
    padding: 12px;
    text-decoration: none;
    font-size: 18px;
    color: black;
    display: block
  }
  
  #movies_list li a:hover:not(.header) {
    background-color: #eee;
  }
  
  .filter_list {
      list-style-type: none;
      width: 100%;
  }